@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@100;200;500;700&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.0/font/bootstrap-icons.css);
:root {
  --breakpoint-xs: 0;
  --breakpoint-sm: 576px;
  --breakpoint-md: 768px;
  --breakpoint-lg: 992px;
  --breakpoint-xl: 1200px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Yanone Kaffeesatz", sans-serif;
  text-decoration: none;
  /* background-color: black; */
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  background: url(https://venngage-wordpress.s3.amazonaws.com/uploads/2018/11/15-Presentation-Background-Examples12.jpeg)
    no-repeat center center fixed;
  background-size: cover;
}


.homeImg {
  max-width: 35rem;
  max-height: 30rem;;
}

.proj-img {
  max-width: 400px;
  max-height: 350px;
}

.portfolio-item:hover {
  transition: all 0.3s ease 0s !important;
  border-radius: 5px;
  -webkit-clip-path: inset(.5rem);
  clip-path: inset(.5rem);
}

.card {
  max-width: 35rem;
  max-height: 35rem;
  -webkit-align-items: center !important;
          align-items: center !important;
  /* white-space: nowrap;
  overflow:visible; */
}

/* header settings for each project */
h3 {
  font-size: 45px;
  /* color: rgb(255, 225, 194); */
  /* -webkit-text-stroke-width: .5px; */
  -webkit-text-stroke-color: rgb(6, 6, 6);
  text-shadow: 1px 1px 1px rgb(19, 16, 16);
  /* font-weight: bolder; */
}

/* span {
font-size: 15px;
} */
.container {
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  /* padding: 10px; */
}

footer {
  text-align: center;
  font-size: 2rem;
  position:relative;
  left:0;
  bottom:0;
  right:0;
  }

  .icons-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px;
    gap: 10px;
    max-width: 300px;
    margin: auto;
    -webkit-align-items: center;
            align-items: center;
    /* position: center; */
  }

  .link-item:hover {
    border-color: rgb(240, 206, 170);
    border-color: rgb(223, 208, 240);
    border-style: solid;
    transition: all 0.3s ease 0s !important;
    border-radius: 5px;
    /* padding: 10px; */
  }

  .about {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 30px;
    gap: 30px;
  }
