  .icons-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 10px;
    max-width: 300px;
    margin: auto;
    align-items: center;
    /* position: center; */
  }

  .link-item:hover {
    border-color: rgb(240, 206, 170);
    border-color: rgb(223, 208, 240);
    border-style: solid;
    transition: all 0.3s ease 0s !important;
    border-radius: 5px;
    /* padding: 10px; */
  }

  